


.bot-des{
    width: 60%;
}
.details-btn{
    margin: 0.8em 0em;
}

.moderate-icon-container{
    display: inline;
    padding: 0.6em;
    margin: 0.8em 0 0.8em 0.8em;
    color: white;
    background-color: rgb(4, 156, 144);
    border-radius: 5px 0px 0px 5px;
}
.details-btn *{
    font-size: 0.8em;
}
.details-buy{
    padding: 0.5em 0.4em;
    border-radius: 0px 5px 5px 0px;
}
.moderate-icon{
    margin-left: 0%;
    padding: 0.4em;
    color: rgb(4, 156, 144);
    border: 0.2em solid rgb(4, 156, 144);
}


@media only screen and (max-width:710px) {
    .bot-des{
        width: 50%;
    }
}